.LeverageSlider {
  margin-top: 1.5rem;
  margin-bottom: 3.4rem;
  padding: 0 0.8rem;

  .rc-slider-tooltip-inner {
    box-shadow: none;
    padding: 0.465rem 0.8rem;
    background: #303652;
  }

  .rc-slider-tooltip {
    z-index: 5;
  }

  .rc-slider-tooltip-placement-top .rc-slider-tooltip-arrow {
    border-top-color: #303652;
  }

  .rc-slider-rail {
    background: linear-gradient(90deg, rgb(53 53 53) 0%, rgb(64, 64, 64) 100%);
  }

  .rc-slider-track {
    background: linear-gradient(90deg, var(--primary-btn-bg), var(--primary-btn-bg));
  }
  .rc-slider-dot {
    border: none;
    border-radius: 1px;
    width: 2px;
    margin-left: -1px;
    background: rgb(53 53 53);
  }
  .rc-slider-dot-active {
    background: var(--primary-btn-bg);
  }
  .rc-slider-handle {
    background: #D9D9D9;
    border: solid 2px var(--primary-btn-bg);
  }

  .rc-slider-handle:active {
    box-shadow: 0 0 0.4rem 0.6rem rgba(252, 128, 45, 0.2);
  }
  .rc-slider-mark-text,
  .rc-slider-mark-text-active {
    color: white;
    opacity: 0.5;
  }

  .rc-slider-mark-text:hover,
  .rc-slider-mark-text-active:hover {
    opacity: 1;
  }
}

.button-container{
 display: flex;
 background: #1a1a1a;
 color:#5F6261;
 justify-content: space-between;
 margin-inline: -0.75rem;
 margin-top: 1.5rem;
 border-radius: 0.3rem;
 font-size: 1.2rem;
}

.button-container-key{
padding-inline: 1.6rem;
padding-block: 1rem;
// border-right: 1px solid #454545;
cursor: pointer;
transition: background 0.3s, border-radius 0.3s;
}


.button-container-key:first-child {
  border-top-left-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;
}

.button-container-key:last-child {
  border-top-right-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
}

.button-container-key:not(:last-child) {
  border-right: 1px solid #454545;
}

.button-container-key:hover{
  background: #242424;
  }

  .button-container-key.active{
    background: #242424;
    }