.NewsList {
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
  position: relative;
  background: #0E0E0E;
  padding: 0;
  overflow: hidden;
  border: 1px solid #2A211C;
}

.NewsList .Exchange-list {
  height: auto;
}

.news-item-content:hover {
  background-color: #211200;
}

.fixed-news-section {
  background-color: #211200;
  padding: 1rem 0.8rem;
  color: #CACACA;
  /* font-family: monospace; */
  width: 100%;
  max-width: 100%;
  max-height: 400px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-sizing: border-box;
  border-radius: 0px 0px 0.8rem 0.8rem;
  border-bottom: 1px solid rgba(221, 88, 0, 0.15);
  background: linear-gradient(0deg, rgba(23, 16, 8, 0.48) 0%, rgba(33, 22, 16, 0.60) 100%), #000;
  box-shadow: 1px 1px 1px 0px #27201C inset, 2px 1px 14px -3px #27201C inset, 0px 8px 9px 0px rgba(0, 0, 0, 0.25) inset;
}

.fixed-news-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0px;
  flex-shrink: 0;
  width: 100%;
}

.fixed-news-header-content {
  flex: 1;
  min-width: 0;
  margin-right: 16px;
}

.fixed-news-metadata {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
  padding-bottom: 10px;
  flex-wrap: wrap;
  width: 100%;
  border-bottom: 1px solid #1A1A1A;
}

.fixed-news-source,
.fixed-news-crypto,
.fixed-news-time {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 100%;
  margin: 0 1rem;
  font-size: 1.25rem;
}

.fixed-news-time {
  color: #fff;
}

.fixed-news-source {
  color: #fff;
}

.fixed-news-crypto {
  color: #FE9500;
  display: inline-block;
  position: relative;
  /* max-width: 120px; */
  overflow: hidden;
  white-space: nowrap;
}

.fixed-news-crypto span {
  display: inline-block;
  position: relative;
}

.fixed-news-crypto span:not([style*="animation: none"]) {
  padding-right: 16px;
  animation: scrollText 8s linear infinite;
}

.fixed-news-crypto:hover span {
  animation-play-state: paused;
}

.fixed-news-title-container {
  display: flex;
  justify-content: space-between;
  align-items: start;
}

.fixed-news-title {
  color: #DD5800;
  font-size: 1.4rem;
  margin-bottom: 0.8rem;
  word-break: break-word;
  width: 100%;
  overflow: hidden;
  white-space: pre-wrap;
  line-height: 1.5;
  padding-right: 8px;
  padding-left: 4rem;
}

.fixed-news-close {
  cursor: pointer;
  margin-left: 0.8rem;
}

.fixed-news-summary-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-height: 200px;
  overflow-y: auto;
  padding-right: 8px;
  width: 100%;
  flex: 1 1 auto;
  padding-left: 4rem;
  padding-bottom: 0.8rem;
}

.fixed-news-summary {
  color: #fff;
  word-break: break-word;
  line-height: 1.5;
  width: 100%;
  white-space: pre-wrap;
  overflow: hidden;
  font-size: 1.25rem;
}

.fixed-news-actions {
  flex: 1;
  color: #FFF;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0.8rem;
}

.ask-fortuna-button {
  background: #FE9500;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.fixed-news-content {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.news-title {
  flex: 1;
  min-width: 0;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
  hyphens: auto;
  font-size: 1.25rem;
}

.news-item-new {
  animation: highlight 2s ease-out;
}

.news-item-content {
  width: 100%;
  box-sizing: border-box;
}

.text-center {
  text-align: center;
  padding: 1rem;
  color: #4C4C4C;
}

@keyframes highlight {
  0% {
    background-color: rgba(254, 149, 0, 0.2);
  }
  100% {
    background-color: transparent;
  }
}

@keyframes scrollText {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-150px);
  }
}

/* Additional styles for improved UI */
.Exchange-list td {
  padding: 0;
}

/* Impact Pie sizing */
.news-item-wrapper .ImpactDoughnut {
  min-width: 14px;
  width: 14px;
  height: 14px;
}

/* Dropdown Filter Styles */
.DropdownFilter {
  width: 100%;
  padding: 16px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .news-item-wrapper {
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .news-title {
    width: 100%;
    margin-top: 0.5rem;
  }

  .fixed-news-content {
    padding: 16px;
  }
}

.news-scroll-container {
  flex: 1;
  position: relative;
  height: 0;
  min-height: 0;
  overflow-y: auto;
  padding: 0;
}

.news-scroll-container.with-fixed-news {
  border-top: 1px solid #282828;
}

.Exchange-middle-container {
  border-radius: 0 0 0 0;
  margin-left: 1.5rem;
}

.Exchange-list {
  width: 100%;
  margin: 0;
  border-collapse: collapse;
}

.Exchange-list th {
  position: sticky;
  top: 0;
  background: #131313;
  z-index: 1;
  padding: 12px 16px;
}

.Exchange-list tr {
  border-bottom: 1px solid rgb(23, 23, 23, 0.5);
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.Exchange-list tr:hover {
  background: linear-gradient(90deg, rgb(59, 59, 59) 0%, rgb(63, 63, 63) 100%);
  box-shadow: inset 0px 0px 3rem 0.5rem rgba(255, 255, 255, 0.01);
}

.news-item {
  transition: background-color 0.2s ease;
}

.news-item.selected {
  background-color: rgba(254, 149, 0, 0.1);
}

.news-item-wrapper {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0 0px;
  width: 100%;
  box-sizing: border-box;
}

.source-label,
.crypto-label,
.time-label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 150px;
  font-size: 1.25rem;
}

.source-label {
  margin: 0 1rem;
  min-width: 10rem;
  max-width: 10rem;
  display: inline-block;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

.crypto-label {
  margin: 0 1rem;
  color: #FE9500;
  min-width: 7rem;
  max-width: 7rem;
  display: inline-block;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

.source-label span,
.crypto-label span {
  display: inline-block;
  position: relative;
}

/* Remove default animations */
.source-label span:not([style*="animation: none"]),
.crypto-label span:not([style*="animation: none"]) {
  animation: none;
  padding-right: 0;
}

/* Apply animation on hover with fixed distance */
tr:hover .source-label span:not([style*="animation: none"]),
tr:hover .crypto-label span:not([style*="animation: none"]) {
  padding-right: 16px;
  animation: scrollText 8s linear infinite;
}

/* Pause animation when hovering directly over the scrolling text */
tr:hover .source-label:hover span,
tr:hover .crypto-label:hover span {
  animation-play-state: paused;
}

.title-separator {
  color: #4C4C4C;
}

/* Ensure consistent padding for all main content containers */
.news-item-wrapper,
.DropdownFilter,
.Exchange-list th {
  padding-right: 16px;
}

.fixed-news-section {
  padding-left: 16px;
  box-sizing: border-box;
}

/* Add new styles */
.Exchange-middle-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0;
  overflow: hidden;
}

.Exchange-middle-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0;
  overflow: hidden;
}

.Exchange-lists-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
  min-height: 0;
  overflow: hidden;
}

.Exchange-left-container,
.Exchange-middle-container {
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow: hidden;
}

/* Add this to ensure the list header doesn't stretch */
.Exchange-list-tab-container {
  flex: 0 0 auto;
}

/* Add height constraint to the main wrapper */
.Exchange-lists-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.Exchange-middle-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0;
  overflow: hidden;
}

.Exchange-middle-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0;
  overflow: hidden;
}
