.FortunaChat {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #000;
  position: relative;
}

.FortunaChat-bg {
  position: absolute;
  width: 70rem;
  height: 30rem;
  right: -10rem;
  bottom: -8rem;
  border-radius: 688.472px;
  opacity: 0.39;
  background: rgba(221, 88, 0, 0.35);
  filter: blur(90px);
  pointer-events: none;
}

.FortunaChat-scroll-container {
  flex: 1;
  overflow-y: auto;
}

.FortunaChat-message-wrapper {
  padding: 0.5rem 0;
  display: flex;
}

.FortunaChat-messages-container {
  padding: 0 1.6rem;
}

.FortunaChat-user-message-wrapper {
  justify-content: flex-end;
}

.FortunaChat-ai-message-wrapper {
  justify-content: flex-start;
}

.FortunaChat-message-inner {
  max-width: 80%;
}

.FortunaChat-message-content {
  color: #fff;
  line-height: 1.6;
  font-size: 1.25rem;
  white-space: pre-wrap;
  padding: 0.75rem 1.2rem;
  border-radius: 4px;
  border: 1px solid rgba(55, 55, 55, 0.5);
}

.FortunaChat-user-message-wrapper .FortunaChat-message-content {
  background: #FE9500;
  padding: 8px;
  border-radius: 4px;
}

.FortunaChat-user-message-wrapper .FortunaChat-message-content > p {
  margin: 0;
}

.FortunaChat-ai-message-wrapper .FortunaChat-message-content {
  background: #1A1A1A;
}

.FortunaChat-message-time {
  color: #666;
  margin-top: 1rem;
  font-size: 1.25rem;
}

.FortunaChat-question-bubbles {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
  padding: 0 1rem;
}

.FortunaChat-question-bubble {
  background: rgba(56, 55, 55, 0.20);
  border: 1px solid #333;
  color: #fff;
  padding: 0.85rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 1.25rem;
  color: #585858;
  font-weight: 600;
}

.FortunaChat-question-bubble:hover {
  /* background: #252525; */
  border-color:rgba(221, 88, 0, 0.4);
  color: rgba(221, 88, 0, 0.60);
}

.FortunaChat-input-container {
  padding: 2rem 1.6rem;
  margin-top: auto;
  z-index: 1;
}

.FortunaChat-input-form {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: #1A1A1A;
  padding: 1.2rem;
  border-radius: 4px;
  border: 1px solid rgba(26, 26, 26, 0.5);
}

.FortunaChat-input {
  flex: 1;
  background: transparent;
  border: none;
  color: #fff;
  padding: 0.75rem;
  font-size: 1.25rem;
}

.FortunaChat-input::placeholder {
  color: #8D8D8D;
  font-style: italic;
}

.FortunaChat-input:focus {
  outline: none;
}

.FortunaChat-submit-button {
  background: #FE9500;
  color: white;
  border: none;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  min-width: 100px;
}

.FortunaChat-submit-button:hover {
  background: #FE9500;
}

.FortunaChat-submit-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

