/* Enhanced Message Content Styling */
.FortunaChat-message-content .message-header {
  font-weight: 600;
  margin: 1em 0 0.5em 0;
  color: #FE9500;
  border-bottom: 1px solid #333;
  padding-bottom: 0.3em;
}

.FortunaChat-message-content .bullet-point {
  margin: 0.5em 0;
  padding-left: 1em;
  display: flex;
  align-items: flex-start;
  gap: 0.5em;
}

.FortunaChat-message-content .bullet {
  color: #FE9500;
  line-height: 1;
}

.FortunaChat-message-content .message-paragraph {
  margin: 0.5em 0;
  line-height: 1.6;
}

.FortunaChat-message-content strong {
  color: #FE9500;
  font-weight: 600;
}

.FortunaChat-message-content .inline-content {
  display: inline-flex;
  align-items: center;
  gap: 0.25em;
}

.FortunaChat-news-container {
  background: #FE9500;
  padding: 2px;
  border-radius: 4px;
  margin-bottom: 8px;
}

.FortunaChat-news-block {
  background: #1b1b1b;
  color: white;
  padding: 12px;
  border-radius: 3px;
  font-weight: 500;
  line-height: 1.4;
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: nowrap;
  width: 100%;
}

.FortunaChat-news-time {
  color: #999;
  font-size: 0.9em;
  white-space: nowrap;
  order: 2;
  flex-shrink: 0;
}

.FortunaChat-news-source {
  color: #FE9500;
  font-weight: 600;
  white-space: nowrap;
  order: 3;
  flex-shrink: 0;
}

.FortunaChat-news-title {
  text-align: left;
  flex: 1;
  order: 4;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 0;
}

.FortunaChat-message-content code {
  background: rgba(0, 0, 0, 0.1);
  padding: 2px 4px;
  border-radius: 4px;
  font-family: monospace;
  font-size: 0.9em;
}

.FortunaChat-message-content pre.code-block {
  background: rgba(0, 0, 0, 0.1);
  padding: 12px;
  border-radius: 6px;
  overflow-x: auto;
  margin: 8px 0;
}

.FortunaChat-message-content pre.code-block code {
  background: none;
  padding: 0;
  white-space: pre;
}

.FortunaChat-message-content h1,
.FortunaChat-message-content h2,
.FortunaChat-message-content h3 {
  margin: 8px 0;
  font-weight: 600;
}

.FortunaChat-message-content h1 { font-size: 1.5em; }
.FortunaChat-message-content h2 { font-size: 1.3em; }
.FortunaChat-message-content h3 { font-size: 1.1em; }

.FortunaChat-message-content li {
  margin-left: 20px;
  list-style-type: disc;
}

.FortunaChat-message-content a {
  color: #007bff;
  text-decoration: none;
}

.FortunaChat-message-content a:hover {
  text-decoration: underline;
}

.FortunaChat-welcome {
  height: calc(100% - 6rem);
  min-height: 16rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.FortunaChat-logo {
  width: 8rem;
  height: 8rem;
  margin-bottom: 0.5rem;
}

.FortunaChat-greeting {
  font-size: 2rem;
  margin-bottom: 0.8rem;
  color: var(--text-primary);
}

.FortunaChat-intro {
  /* color: var(--text-secondary); */
  color: #C1C1C1;
  font-size: 1.25rem;
  line-height: 1.2;
  max-width: 600px;
  margin: 0 auto;
}

/* Add these new styles */
.FortunaChat-header {
  display: flex;
  justify-content: flex-end;
  padding: 1rem 0;
}

.FortunaChat-clear-button {
  background: #1A1A1A;
  color: #fff;
  border: 1px solid #333;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.25rem;
  transition: all 0.2s;
}

.FortunaChat-clear-button:hover {
  background: #252525;
  border-color: #FE9500;
  color: #FE9500;
}
