/* Referral.css */
.referrals-container {
  min-height: 100vh;
  background-color: #000000;
  padding: 2rem 1rem;
  padding-bottom: 20rem;
}

.referrals-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.referrals-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  margin-bottom: 1rem;
  letter-spacing: -0.025em;
  line-height: 1.2;
}

.referrals-subtitle {
  text-align: center;
  color: #a9a9b0;
  margin-bottom: 4rem;
  font-size: 1.25rem;
  line-height: 1.6;
}

.highlight {
  color: #f59e0b;
  font-weight: 600;
}

/* Loading State */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  color: #f59e0b;
  font-size: 1.2rem;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
}

/* Error Message */
.error-message {
  background-color: #991b1b;
  color: #fecaca;
  padding: 1rem;
  border-radius: 0.5rem;
  margin: 1rem 0;
  text-align: center;
  border: 1px solid #ef4444;
  animation: fadeIn 0.3s ease;
}

/* Stats Section */
.referral-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-bottom: 4rem;
}

.stat-box {
  background-color: #18181b;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  border: 1px solid #27272a;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-box:hover {
  transform: translateY(-2px);
  border-color: #3f3f46;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.stat-box::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #f59e0b, transparent);
  transform: scaleX(0);
  transition: transform 0.5s ease;
}

.stat-box:hover::after {
  transform: scaleX(1);
}

.stat-box h3 {
  color: #a9a9b0;
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.stat-box .stat-value {
  color: #ffffff;
  font-size: 2.25rem;
  font-weight: 700;
  letter-spacing: -0.025em;
  text-shadow: 0 0 20px rgba(245, 158, 11, 0.1);
}

/* Code Section */
.code-section {
  background-color: #18181b;
  border-radius: 1rem;
  margin-bottom: 4rem;
  overflow: hidden;
  border: 1px solid #27272a;
  transition: all 0.3s ease;
}

.code-section:hover {
  border-color: #3f3f46;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.code-tabs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  border-bottom: 1px solid #27272a;
  background-color: #1f1f23;
}

.tab-button {
  background: none;
  border: none;
  padding: 1.25rem;
  color: #a9a9b0;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
}

.tab-button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background-color: #f59e0b;
  transition: all 0.3s ease;
  transform: translateX(-50%);
}

.tab-button:hover:not(.active) {
  color: #ffffff;
  background-color: #27272a;
}

.tab-button.active {
  color: #ffffff;
  background-color: #18181b;
}

.tab-button.active::after {
  width: 100%;
}

.code-content {
  padding: 3rem 2rem;
  text-align: center;
}

.code-content h3 {
  color: #ffffff;
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.code-input {
  width: 100%;
  max-width: 400px;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: #27272a;
  border: 2px solid #3f3f46;
  color: #ffffff;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.code-input:focus {
  outline: none;
  border-color: #f59e0b;
  box-shadow: 0 0 0 2px rgba(245, 158, 11, 0.2);
}

.code-button {
  background-color: #f59e0b;
  color: #000000;
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 1.5rem;
}

.code-button:not(:disabled):hover {
  background-color: #d97706;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.2);
}

.code-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background-color: #3f3f46;
}

/* Table Styles */
.referrals-table {
  background-color: #18181b;
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 2rem;
  border: 1px solid #27272a;
  transition: all 0.3s ease;
}

.referrals-table:hover {
  border-color: #3f3f46;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.table-header {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  padding: 1rem 1.5rem;
  color: #a9a9b0;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 600;
  border-bottom: 1px solid #27272a;
  background-color: #1f1f23;
  border-radius: 0.5rem 0.5rem 0 0;
}

.code-button {
  margin-left: 0.5rem;
}

.table-row {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  padding: 1rem 1.5rem;
  color: #ffffff;
  border-bottom: 1px solid #27272a;
  transition: all 0.3s ease;
  align-items: center;
}

.table-row:hover {
  background-color: #27272a;
  transform: translateX(4px);
  box-shadow: inset 0 0 0 1px #3f3f46;
}

.table-row:last-child {
  border-bottom: none;
  border-radius: 0 0 0.5rem 0.5rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 1024px) {
  .referral-stats {
    gap: 1.5rem;
  }
  
  .stat-box {
    padding: 1.5rem;
  }
  
  .stat-box .stat-value {
    font-size: 2rem;
  }
}

@media (max-width: 768px) {
  .referrals-container {
    padding: 1.5rem 1rem;
  }

  .referral-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .table-header,
  .table-row {
    grid-template-columns: repeat(3, 1fr);
    font-size: 0.875rem;
    padding: 0.75rem 1rem;
  }

  .table-header div:nth-child(n + 4),
  .table-row div:nth-child(n + 4) {
    display: none;
  }

  .pagination {
    gap: 1rem;
  }
}

@media (max-width: 640px) {
  .referrals-title {
    font-size: 2rem;
  }

  .referrals-subtitle {
    font-size: 1.1rem;
    margin-bottom: 3rem;
  }

  .referral-stats {
    grid-template-columns: 1fr;
  }

  .code-content {
    padding: 2rem 1rem;
  }

  .table-header,
  .table-row {
    grid-template-columns: 1fr 1fr;
  }

  .table-header div:nth-child(n + 3),
  .table-row div:nth-child(n + 3) {
    display: none;
  }

  .pagination-button {
    min-width: 5rem;
    padding: 0.5rem 1rem;
  }
}
.referrer-code-section {
  background-color: #18181b;
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 2rem;
  border: 1px solid #27272a;
  text-align: center;
}

.referrer-code-section h3 {
  color: #ffffff;
  margin-bottom: 1rem;
  font-size: 1.25rem;
}

.code-display {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  margin: 0 1rem;
}

.code-display .code {
  background-color: #27272a;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  color: #f59e0b;
  font-family: monospace;
  font-size: 1.1rem;
  letter-spacing: 0.05em;
  display: inline-block;
}

.code-display .copy-button {
  background-color: #3f3f46;
  color: #ffffff;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.2s ease;
}

.code-display .copy-button:hover {
  background-color: #52525b;
  transform: translateY(-1px);
}

@media (max-width: 768px) {
  .code-display {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
}