/* Leaderboard.css - Updated to match GlpSwap styling */
.leaderboard-container {
  min-height: 100vh;
  background: #000000;
  padding: 2rem 1rem;
}

.leaderboard-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Updated title styling to match Tab-title */
.leaderboard-title {
  letter-spacing: 0px;
  color: #ffffff;
  font-size: 3.41rem;
  font-weight: 500;
  text-align: center;
  margin-bottom: 1rem;
  line-height: 1.2;
}

/* Updated subtitle styling to match Tab-description */
.leaderboard-subtitle {
  letter-spacing: 0px;
  color: #a9a9b0;
  margin-top: 0.8rem;
  text-align: center;
  margin-bottom: 4rem;
  font-size: 1.25rem;
  line-height: 1.6;
}

.highlight {
  color: #f89191; /* Updated to match GlpSwap accent color */
  font-weight: 600;
}

/* Loading State - updated to match GlpSwap background */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  color: #f89191;
  font-size: 1.2rem;
  background: #000000;
  backdrop-filter: blur(5px);
}

/* Stats Section - updated to match GlpSwap card styling */
.leaderboard-stats {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4rem;
  background: #000000;
}

.stat-box {
  background: #191919;
  padding: 1.5rem;
  border-radius: 0.4rem;
  text-align: center;
  border: 1px solid #2C2C2C;
  transition: all 0.3s ease;
  position: relative;
  margin-right: 1.5rem;
  width: 100%;
}

.stat-box:last-child {
  margin-right: 0;
}

.stat-box h3 {
  color: #a9a9b0;
  font-size: 1.2rem;
  font-weight: normal;
  margin-bottom: 1rem;
  letter-spacing: 0px;
}

.stat-box .stat-value {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 500;
  letter-spacing: 0px;
}

/* Table Styles - updated to match GlpSwap token-list styling */
.leaderboard-table {
  background: #191919;
  border-radius: 0.4rem;
  padding: 1.5rem;
  padding-top: 0;
  margin-bottom: 2rem;
  border: 1px solid #2C2C2C;
}

.table-header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 1.1625rem;
  color: #a9a9b0;
  font-size: var(--font-base);
  text-transform: uppercase;
  letter-spacing: 0.25px;
  font-weight: normal;
  border-bottom: 1px solid #2C2C2C;
}

.table-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 1.1625rem;
  color: #ffffffde;
  border-bottom: 1px solid #2C2C2C;
  transition: background 0.3s ease;
  align-items: center;
}

.table-row:hover {
  background: #2A2A2A;
}

/* Pagination - updated to match GlpSwap button styling */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}

.pagination-button {
  border: none;
  border-radius: 9999px;
  background: rgba(38, 38, 38, 1);
  color: #ffffff;
  font-weight: normal;
  padding: 0.75rem 1.5rem;
  height: 3.5rem;
  cursor: pointer;
  transition: background 0.3s ease;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.pagination-button:hover {
  background: rgba(64, 64, 64, 1);
}

.pagination-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination-info {
  color: #a9a9b0;
  font-size: var(--font-base);
  letter-spacing: 0.25px;
}

.connect-wallet-message {
  background: #191919;
  color: #f89191;
  padding: 1.5rem;
  border-radius: 0.4rem;
  text-align: center;
  margin: 2rem auto;
  font-weight: normal;
  max-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  border: 1px solid #2C2C2C;
}

/* Responsive Styles */
@media (max-width: 900px) {
  .leaderboard-stats {
    flex-wrap: wrap;
  }

  .stat-box {
    margin: 0;
    margin-bottom: 1.5rem;
    width: 100%;
  }
}

@media (max-width: 768px) {
  .leaderboard-title {
    font-size: 2.5rem;
  }

  .table-header,
  .table-row {
    padding: 0.75rem;
  }
}

@media (max-width: 600px) {
  .leaderboard-title {
    font-size: 2rem;
  }

  .leaderboard-subtitle {
    font-size: 1rem;
  }

  .stat-box {
    padding: 1rem;
  }

  .pagination-button {
    padding: 0.5rem 1rem;
    font-size: 1.25rem;
    height: 2.5rem;
  }
}

/* Animations - kept from original but simplified */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Skeleton Loading - updated to match GlpSwap colors */
.skeleton-row {
  opacity: 0.7;
  pointer-events: none;
}

.skeleton-cell {
  height: 20px;
  background: linear-gradient(90deg, #191919 25%, #2C2C2C 50%, #191919 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
  border-radius: 4px;
}

@keyframes shimmer {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

/* Error message - updated to match GlpSwap styling */
.error-message {
  background: #191919;
  color: #f89191;
  padding: 1rem;
  border-radius: 0.4rem;
  margin: 1rem 0;
  text-align: center;
  border: 1px solid #2C2C2C;
  animation: fadeIn 0.3s ease;
}

/* Card Header Styles */
.leaderboard-table h2 {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  letter-spacing: 0px;
}