.satoshi-button {
  cursor: pointer;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  border-radius: 0.3rem;
  font-size: 1.05rem;
  font-weight: bold;
  border: 1px solid #a04000;
  padding: 0 1.6rem;
  height: 2.6rem;
  user-select: none;

  &.primary {
    background-color: #dd5800;
    color: #fff;
    box-shadow: 0px -1px 0px #dd5800, 0px 1px 0px #dd5800;

    &:not(.disabled):hover {
      color: rgba(255, 255, 255, 0.6);
      background-color: rgba(221, 88, 0, 0.9);
    }
  }

  &.outline {
    background-color: #261408;
    color: #FF812E;

    &:not(.disabled):hover {
      color: rgba(255, 129, 46, 0.6);
      background-color: rgba(38, 20, 8, 0.9);
    }
  }

  &.disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }

  &.small {
    font-size: 1.05remrem;
  }

  &.medium {
    font-size: 1.2rem;
  }

  &.large {
    // font-size: 1.4rem;
  }

  .decoration-border-outer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .decoration-border {
    width: 100%;
    height: 100%;
    padding: 0.1rem;
  }

  .decoration-border-inner {
    width: 100%;
    height: 100%;
    border: 1px solid rgba(160, 64, 0, 0.25);
    border-radius: 0.3rem;
  }

  .decoration {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.05rem;
  }
}
