.sCheckbox {
  user-select: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.sCheckbox.disabled {
  cursor: default;
  pointer-events: none;
}

.sCheckbox-icon-wrapper {
  display: inline-flex;
  align-items: center;
}

.sCheckbox .sCheckbox-icon {
  font-size: 1.6rem;
  color: #DD5800;
  margin-bottom: 0;
}

.sCheckbox.fullRow .sCheckbox-icon {
  margin-right: 0;
}

.sCheckbox.disabled .sCheckbox-icon {
  color: #47509e;
}

.sCheckbox-icon.inactive {
  color: #DD5800;
}

.sCheckbox-label {
  display: inline-block;
  vertical-align: middle;
  font-size: var(--font-sm);
  text-align: left;
}

.sCheckbox.fullRow {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto;
  direction: rtl;
  font-size: var(--font-sm);
  margin-bottom: 0.465rem;
}
