  opacity: 0.7;
  /* text-decoration: underline; */
}

.ExchangeChart-top-inner {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto auto auto auto;
  align-items: center;


}
.ExchangeChart-grid-item {
  border-right: 1px solid #282828; /* Adds a border to each grid item */
  /* padding-block: 0.75rem; Optional: Add some padding */
  padding-inline: 0.75rem;
  height: 100%;
}

.ExchangeChart-grid-item-end {
  padding-inline: 0.75rem;
  height: 100%;
}

.ExchangeChart-grid-item-logo-outer {
  border-right: 1px solid #282828; /* Adds a border to each grid item */
