/* GlpSwap.css */
.GlpSwap-content {
  display: flex;
  justify-content: space-between;
  border-bottom-left-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
}

.Tab-title {
  letter-spacing: 0px;
  color: #ffffff;
  font-size: 3.41rem;
  font-weight: 500;
}

.Tab-description {
  letter-spacing: 0px;
  color: #a9a9b0;
  margin-top: 0.8rem;
}

.Tab-description span {
  color: #f89191;
}

.GlpSwap-from-token .Modal {
  align-items: flex-start;
  justify-content: flex-end;
}

.GlpSwap-content .GlpSwap-box {
  width: 100%;
  /* outline: 1px solid #2C2C2C; */
}



.GlpSwap-content .GlpSwap-stats-card {
  width: 100%;
  max-width: -webkit-fill-available;
  margin-right: 1.5rem;
  /* background: linear-gradient(to top right, rgba(33, 33, 33, 0.975), rgba(0, 0, 0, 0.975)); */
  background: #000000;
}

.GlpSwap-content .GlpSwap-stats-card-mobile {
  width: 100%;
  max-width: -webkit-fill-available;
  /* background: linear-gradient(to top right, rgba(33, 33, 33, 0.975), rgba(0, 0, 0, 0.975)); */
  background: #000000;
  padding-bottom: 0.5rem;
  padding-inline: 1.5rem;
}



.GlpSwap-stats-card .App-card-row .label {
  color: #a9a9b0;
  opacity: 1;
}

.GlpSwap-content .GlpSwap-box {
  max-width: 39.95rem;
  border-radius: 0.4rem;
  padding: 1.5rem;
  padding-top: 0;
  height: fit-content;
}

.GlpSwap-box .Exchange-swap-option-tabs {
  margin: 1.5rem 0;
  background: #1A1A1A;
}
.time-tab-bg{
background: #1a1a1a !important;
}

.App-card-title{
/* outline: 1px solid #2C2C2C;
border-radius: 0.25rem; */
}

.App-card-title-tgl-btn{
  font-size: var(--font-md);
  line-height: 2rem;
  font-weight: normal;
  letter-spacing: 0px;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tog-btn-font-size{
font-size: 1.2rem;
align-items: center;
  justify-content: space-between;
  display: flex;
}
.tog-btn{
  padding-inline: 0.75rem;
  padding-block: 0.5rem;
}
.tog-btn:hover{
  background: #79563a8f;
}
.card-outline{
  outline: 1px solid #2C2C2C;
  /* border-radius: 0.25rem; */
}

.stats-bar-bg{
  background: linear-gradient(to right, rgba(0, 0, 0, 0.975),rgba(0, 0, 0, 0.975), rgba(33, 33, 33, 0.975));
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}

.App-card-title-mark-info{
padding-inline: 1rem;
padding-block: 0.5rem;
}

.divider-stats-bar{
  /* border-right: 1px solid #2C2C2C; */
  padding-block: 2rem;
  padding-inline: 3.5rem;  
}

.divider-stats-bar-mobile{
  /* border-right: 1px solid #2C2C2C; */
  /* padding-inline: 1.5rem;   */
  min-width: 11rem;
}
.divider-stats-bar-end{
  margin-block: 0.5rem;
  padding-inline: 1.5rem;
}
.divider-stats-bar-token{
  /* border-right: 1px solid #2C2C2C; */
  margin-block: 0.5rem;
}

.divider-bar{
  /* border-right: 1px solid #2C2C2C; */
  padding-block: 1.5rem;
  padding-inline: 1.5rem;  
}

.divider-bar-mobile{
  /* border-right: 1px solid #2C2C2C; */
  padding-block: 1.5rem;
  min-width: 11.5rem;
}

.divider-bar-tvl{
  padding-block: 2rem;
  padding-inline: 1.5rem; 
}
.divider-bar-tvl-end{
  border-left: 1px solid #2C2C2C;

  padding-inline: 1.5rem; 
  margin-block:2rem;
}


.App-card-title-mark-icon {
  position: relative;
  margin-right: 0.8rem;
  display: flex;
}

.App-card-title-mark-icon .selected-network-symbol {
  position: absolute;
  bottom: 0;
  right: 0;
  border: 1px solid #1e1f37;
  border-radius: 50%;
}

.App-card-title-mark {
  display: flex;
}

.App-card-title-mark .App-card-title-mark-title-stats {
  font-size: 2rem;
  line-height: 2.1rem;
  letter-spacing: 0px;
  color: #ffffff;
}

.App-card-title-mark .App-card-title-mark-title-stats-mobile {
  font-size: 1.25rem;
  line-height: 2.1rem;
  letter-spacing: 0px;
  color: #ffffff;
}

.App-card-title-mark .App-card-title-mark-title-tvl {
  font-size: 4rem;
  line-height: 2.1rem;
  letter-spacing: 0px;
  color: #ffffff;
  margin-block: 1.5rem;
}

.App-card-title-mark .App-card-title-mark-title-tvl-mobile {
  font-size: 2rem;
  line-height: 2.1rem;
  letter-spacing: 0px;
  color: #ffffff;
  margin-block: 1.5rem;
}

.App-card-title-mark .App-card-title-mark-subtitle {
  font-size: 1.2rem;
  line-height: 1.5rem;
  letter-spacing: 0px;
  color: #a9a9b0;
  font-weight: bold;
}

.App-card-title-mark .App-card-title-mark-subtitle-sm {
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: 0px;
  color: #a9a9b0;
  font-weight: bold;
}


.App-card-title-mark .App-card-title-mark-subtitle-mobile {
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: 0px;
  color: #a9a9b0;
  font-weight: bold;
}

.GlpSwap-token-list {
  border-radius: 4px;
  background: #191919;
  padding: 1.5rem;
  padding-top: 0;
  text-align: left;
}

.GlpSwap-token-list-content {
  display: grid;
  padding: 1.5rem;
  padding-top: 0;
  text-align: left;
}

.GlpSwap-box .App-card-title {
  margin-bottom: 0.8rem;
}

.GlpSwap-box .Exchange-info-row {
  height: 1.5rem;
  margin-bottom: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.GlpSwap-box .Exchange-info-row .Exchange-info-label {
  margin-right: 0.8rem;
  color: #a9a9b0;
  opacity: 1;
}

.GlpSwap-cta {
  margin: 0 auto;
  text-align: center;
  padding-top: 0;
}

.GlpSwap-cta .Exchange-swap-button {
  margin: 0 auto;
  justify-content: center;
  min-height: 4.8rem;
  font-size: var(--font-sm);
}

.token-table {
  margin-left: -1.5rem;
  margin-right: -1.5rem;
  width: calc(100% + 3.1rem);
  border-spacing: 0;
  white-space: nowrap;
  font-size: var(--font-base);
}

.token-grid {
  display: none;
}

.token-table td,
.token-table th {
  padding: 1.1625rem;
}

.token-table td:nth-child(2),
.token-table th:nth-child(2) {
  width: 11rem;
}

.token-table td:nth-child(3),
.token-table th:nth-child(3) {
  width: 32rem;
}

.token-table td:nth-child(4),
.token-table th:nth-child(4) {
  width: 20rem;
}

.token-table td:nth-child(5),
.token-table th:nth-child(5) {
  width: 13rem;
}

.token-table td {
  letter-spacing: 0.25px;
  color: #ffffffde;
}

.token-table th:first-child,
.token-table td:first-child {
  padding-left: 1.5rem;
  width: 26.35rem;
}

.token-table th:last-child,
.token-table td:last-child {
  padding-right: 1.5rem;
  text-align: right;
  width: 17rem;
}

.token-table th {
  font-weight: normal;
  letter-spacing: 0.25px;
  color: #a9a9b0;
  text-transform: uppercase;
}

.token-table .action-btn {
  width: 100%;
  justify-content: center;
}
.cap-reached {
  margin-left: 0.8rem;
}

.AppOrder-ball-container {
  position: relative;
  z-index: 2;
}

.AppOrder-ball-icon {
  text-align: center;
  display: block;
  font-size: var(--font-lg);
  opacity: 0.7;
}

.AppOrder-ball-icon {
  text-align: center;
  display: block;
  font-size: 2rem;
  opacity: 0.7;
}

.AppOrder-ball {
  position: absolute;
  width: 3.5rem;
  height: 3.5rem;
  left: 50%;
  margin-left: -1.7825rem;
  top: -2.5rem;
  border-radius: 3.1rem;
  cursor: pointer;
  user-select: none;
  background: var(--primary-btn-bg);
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  color: white;
}

.AppOrder-ball:hover {
  background: var(--primary-btn-hover);
}

.AppOrder-ball:active {
  background: var(--primary-btn-active);
}

.AppOrder-ball:hover .AppOrder-ball-icon {
  opacity: 1;
}

@media (max-width: 900px) {
  .GlpSwap-content {
    flex-wrap: wrap;
  }

  .GlpSwap-content .GlpSwap-stats-card {
    margin: 0;
    margin-bottom: 3.1rem;
    max-width: 100%;
  }

  .GlpSwap-content .GlpSwap-box {
    margin: 0;
    max-width: 100%;
  }
}

@media (max-width: 1100px) {
  .GlpSwap-from-token .Modal {
    align-items: center;
    justify-content: center;
  }

  .GlpSwap-token-list {
    border: none;
    background-color: transparent;
    padding: 0;
  }

  .GlpSwap-token-list-content {
    padding: 0;
  }

  .token-table {
    display: none;
  }

  .token-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.5rem;
    margin-top: 0.8rem;
  }
}
@media (max-width: 600px) {
  .token-grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1.5rem;
    margin-top: 0.8rem;
  }
}

.hover-opacity {
  border: none;
  border-radius: 9999px; /* Very large value for pill shape */
  background: rgba(38, 38, 38, 1); /* Dark gray background */
  font-weight: normal;
  color: white;
   /* Adjust padding for better text fit */
  margin: 0;
  font-size: 1.5rem; /* Adjust as needed */
  text-align: center;
  width: fit-content; /* Allow button to size to content */
  height: 3.5rem;
  width: 19.5rem;
  margin-block: auto;
}

.hover-opacity:hover {
  background: rgba(64, 64, 64, 1); /* Slightly lighter gray on hover */
  cursor: pointer;
}

.moded-v:hover{
  background: #2A2A2A !important; /* 100% opacity on hover */
  cursor: pointer;
}

.App-card-title-mark-info.moded-v.selected {
  background: #282828 !important; /* 100% opacity on selection */
  cursor: pointer;
}