.Tab.block {
  display: grid;
  grid-auto-flow: column;
  font-size: var(--font-sm);
  border-radius: 3px;
  overflow: hidden;
  color: rgba(255, 255, 255, 0.7);
  background: #000000;
  box-shadow: inset 0px 0px 30px 5px rgba(255, 255, 255, 0.01);
}

.Tab-option-icon {
  margin-right: 0.465rem;
  opacity: 0.7;
}

.Tab.block .Tab-option {
  text-align: center;
  padding: 0.8rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  cursor: pointer;
}

.Tab.block .Tab-option-mobile {
  text-align: center;
  padding: 0.8rem;
  padding-inline: 2.25rem;
  cursor: pointer;
}

.Tab.block .Tab-option-mobile-currency {
  text-align: center;
  padding: 0.8rem;
  padding-inline: 6.1rem;
  cursor: pointer;
}

.Tab.block .Tab-option-form-long {
  text-align: center;
  padding-inline: 0.8rem;
  padding-block: 1.2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  cursor: pointer;
}

.Tab.block .Tab-option-form-short {
  text-align: center;
  padding-inline: 0.8rem;
  padding-block: 1.2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  cursor: pointer;
}

.Tab.block .Tab-option:hover {
  color: rgba(255, 255, 255, 1);
  background: #79563a8f;
}

.Tab.block .Tab-option-form-long:hover {
  color: rgba(255, 255, 255, 1);
  background: rgba(208, 219, 218, 0.1);
}

.Tab.block .Tab-option-form-short:hover {
  color: rgba(255, 255, 255, 1);
  background: rgba(208, 219, 218, 0.1);
}

.Tab.block .Tab-option.active{
  opacity: 1;
  pointer-events: none;
  background: var(--primary-btn-bg);
  color: rgba(255, 255, 255, 1);
}

.Tab.block .Tab-option-mobile.active{
  opacity: 1;
  pointer-events: none;
  background: var(--primary-btn-bg);
  color: rgba(255, 255, 255, 1);
}

.Tab.block .Tab-option-mobile-currency.active{
  opacity: 1;
  pointer-events: none;
  background: var(--primary-btn-bg);
  color: rgba(255, 255, 255, 1);
}


.Tab.block .Tab-option-form-long.active {
  opacity: 1;
  pointer-events: none;
  background: #00A479;
  color: rgba(255, 255, 255, 1);
}

.Tab.block .Tab-option-form-short.active {
  opacity: 1;
  pointer-events: none;
  background: rgb(188, 36, 74);
  color: rgba(255, 255, 255, 1);
}

.Tab.block .Tab-option.active .Tab-option-icon {
  opacity: 1;
}

.Tab.inline {
  overflow-x: scroll;
  white-space: nowrap;
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
}

.Tab.inline::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.Tab.inline .Tab-option {
  cursor: pointer;
  display: inline-block;
  font-size: var(--font-base);
  border-bottom: 1px solid #2c2c2c; 
  padding-bottom: 1rem;
}

.Tab.inline .Tab-option.mobile {
  padding-inline: 2.9rem;
}

.Tab.inline .Tab-option.desktop {
  padding-inline: 3rem;
}

.Tab.inline .Tab-option:hover {
  opacity: 0.8;
}

.Tab.inline .Tab-option.active {
  opacity: 1;
  pointer-events: none;
  border-bottom: 2px solid var(--primary-btn-bg); 
}

.Tab.inline.Exchange-list-tabs .Tab-option{
  padding-inline: 1.4rem;
  padding-top: 1rem;
  border-bottom:none;

}

.Tab.inline.Exchange-list-tabs .Tab-option.active{
  color: var(--primary-btn-bg);
  border-bottom:none;
}
